import React, {memo, useEffect, useState} from 'react';
import Header from '../../partials/Header'
import SideBar from '../Partials/SideBar'
import {useSelector, useDispatch} from "react-redux";
import {Link} from 'react-router-dom';
import {ACCOUTN_TYPE} from "../../../../services/Authentication/Types";

import {Get_all_doctors  , Get_all_Patient} from "../../../../entity/Redux/Admin/Action";

export default memo(() => {

    const dispatch = useDispatch();
    // const dashboard = createStore(state => state.dashboard);
    const [doctorslist , setDoctors] = useState({});

    const Admin = useSelector(state => state.Admin);

    useEffect(()=>{
        dispatch(Get_all_doctors())
        dispatch(Get_all_Patient())
    }, [])

    var counter = 0;
    // console.log("users : " , localStorage.getItem(ACCOUTN_TYPE))
    return (
        <div>
            <Header/>


            <div id="main">
                <div className="wrapper">


                    <SideBar/>

                    <div id="back_shado"></div>

                    <section id="content">
                        <div className="container">
                            <div id="card-stats">
                                <div className="row mt-1">
                                    <div className="col s12 m6 l3">
                                        <div
                                            className="card gradient-45deg-light-blue-cyan gradient-shadow min-height-100 white-text">

                                            <div id="back_icon"><i className="material-icons">perm_identity</i>
                                            </div>
                                            <div className="padding-4">
                                                <div className="col s7 m7">
                                                    <i className="material-icons background-round mt-5">perm_identity</i>
                                                </div>

                                                <div className="col s5 m5 right-align">
                                                    <h5 className="mb-0">{Admin.patients ? Admin.patients.length : 0}</h5>
                                                    <p className="no-margin">all patients</p>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                    <div className="col s12 m6 l3">
                                        <div
                                            className="card gradient-45deg-red-pink gradient-shadow min-height-100 white-text">
                                            <div id="back_icon">
                                                <img src="img/icons/doctorB.png"/>
                                            </div>
                                            <div className="padding-4">
                                                <div className="col s7 m7">
                                                    <img src="img/icons/doctor.png"
                                                         className="background-round mt-5"/>
                                                </div>
                                                <div className="col s5 m5 right-align">
                                                    <h5 className="mb-0">{Admin.doctors ? Admin.doctors.length : 0}</h5>
                                                    <p className="no-margin">Doctors</p>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                    <div className="col s12 m6 l3">
                                        <div
                                            className="card gradient-45deg-amber-amber gradient-shadow min-height-100 white-text">
                                            <div id="back_icon">
                                                <img src="img/icons/consultationB.png"/>
                                            </div>
                                            <div className="padding-4">
                                                <div className="col s7 m7">
                                                    <img src="img/icons/consultation.png"
                                                         className="background-round mt-5"/>
                                                </div>
                                                <div className="col s5 m5 right-align">
                                                    <h5 className="mb-0">125</h5>
                                                    <p className="no-margin">Consulting</p>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                    <div className="col s12 m6 l3">
                                        <div
                                            className="card gradient-45deg-green-teal gradient-shadow min-height-100 white-text">
                                            <div id="back_icon"><i className="material-icons">help_outline</i></div>
                                            <div className="padding-4">
                                                <div className="col s7 m7">
                                                    <i className="material-icons background-round mt-5">help_outline</i>
                                                </div>
                                                <div className="col s5 m5 right-align">
                                                    <h5 className="mb-0">220</h5>
                                                    <p className="no-margin">Question</p>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>


                            <div id="work-collections">
                                <div className="row">
                                    <div className="col s12 m12 l6">
                                        <ul id="projects-collection" className="collection z-depth-1">
                                            <li className="collection-item avatar">
                                                <img src="img/icons/doctorB.png"
                                                     style={{position: 'absolute', width: 40, left: 15}}/>
                                                <h6 className="collection-header m-0">Doctor(s)</h6>
                                                <p>List of new doctors</p>

                                                <Link to={"/doctors-list"}><span id={"show_all"}>show All</span></Link>
                                            </li>

                                            <table style={{background: '#222222'}}>
                                                <tbody>


                                                {Admin.doctors ? Admin.doctors.map(item => {
                                                    counter ++;
                                                    if (counter <6)
                                                    {
                                                        return (
                                                            <tr>
                                                                <td>{item.fullName}</td>
                                                                <td><span className="task-cat red">41</span> patient</td>
                                                                <td>
                                                                    <a className={"btn waves-effect green darken-4"} style={{textTransform : 'unset'}}>view more</a>
                                                                </td>
                                                            </tr>
                                                        )
                                                    }else{
                                                    }

                                                }) : <p>please wait ...</p>}



                                                </tbody>
                                            </table>

                                        </ul>
                                    </div>


                                    <div className="col s12 m12 l6">
                                        <ul id="projects-collection" className="collection z-depth-1">
                                            <li className="collection-item avatar">
                                                <i className="material-icons" id="custom_icon">group</i>
                                                <h6 className="collection-header m-0">Patient(s)</h6>
                                                <p>List of patients</p>

                                                <Link to={"/patient-list"}><span id={"show_all"}>show All</span></Link>

                                            </li>

                                            <table style={{background: '#222222'}}>
                                                <tbody>


                                                {Admin.patients ? Admin.patients.map(item => {
                                                    counter ++;
                                                    if (counter <6)
                                                    {
                                                        return (
                                                            <tr>
                                                                <td>{item.id}</td>
                                                                <td>{item.age}</td>
                                                                <td>{item.carrer}</td>

                                                            </tr>
                                                        )
                                                    }else{
                                                    }

                                                }) : <p>please wait ...</p>}



                                                </tbody>
                                            </table>

                                        </ul>
                                    </div>


                                </div>
                            </div>
                        </div>
                    </section>

                </div>
            </div>


        </div>
    );
})